import React, { Component } from 'react';
import Logo from '../Logo/logo';
import FooterMeinmenu from './footer_meinmenu';
import FooterSubmenu from './footer_submenu';
import SocialNetwork from './social_network';

class Footer extends Component {
    constructor() {
        super();

        this.state = {
            showList: false,
            showListSub: false
        }
        this.showList = this.showList.bind(this);
        this.closeList = this.closeList.bind(this);

        this.showListSub = this.showListSub.bind(this);
        this.closeListSub = this.closeListSub.bind(this);
    }

    showList(event) {
        event.preventDefault();

        this.setState({showList: true}, () => {
            document.addEventListener('click', this.closeList);
        });
    }

    closeList(event) {
        if (!this.dropdownList.contains(event.target)) {
            this.setState({showList: false}, () => {
                document.removeEventListener('click', this.closeList);
            });
        }
    }

    showListSub(event) {
        event.preventDefault();

        this.setState({showListSub: true}, () => {
            document.addEventListener('click', this.closeListSub);
        });
    }

    closeListSub(event) {
        if (!this.dropdownList.contains(event.target)) {
            this.setState({showListSub: false}, () => {
                document.removeEventListener('click', this.closeListSub);
            });
        }
    }

    render() {
        return (
            <div className="footer">
                <div className="footer__layout">
                    <div className="footer__logo-block">
                        <Logo />
                        <p className="footer__text">       </p>
                    </div>
                    <div className="footer__desktop">
                        <h3 className="footer__title"> </h3>
                        <FooterMeinmenu />
                    </div>
                    <div className="footer__mobile">
                        <h3 className="footer__title" onClick={this.showList}> </h3>
                        {
                            this.state.showList
                            ? (
                                <div
                                    ref={(element) => {
                                        this.dropdownList = element;
                                    }}
                                >
                                    <FooterMeinmenu />
                                </div>
                            ) : (null)
                        }
                    </div>

                    <div className="footer__desktop">
                        <h3 className="footer__title"> </h3>
                        <FooterSubmenu />
                    </div>
                    <div className="footer__mobile">
                        <h3 className="footer__title" onClick={this.showListSub}> </h3>
                        {
                            this.state.showListSub
                            ? (
                                <div
                                    ref={(element) => {
                                        this.dropdownList = element;
                                    }}
                                >
                                    <FooterSubmenu />
                                </div>
                            ) : (null)
                        }
                    </div>

                    <div className="social-container">
                        <h3 className="footer__title socials-title">  </h3>
                        <SocialNetwork />
                    </div>
                </div>
                <p className="footer__copyright"> 2018 Electrification.</p>
            </div>
        );
    }
}

export default Footer;


