import React from 'react';

const PriceBottom = () => {
    return (
        <div className="price-top">
            <p className="price-top__plain-text">
                 /       .              ,     ,       ,            .      .
            </p>

            <ul className="price-top__list"> 
                <li className="price-top__item">        ,    .</li>
                <li className="price-top__item">       -   ,     ,       .</li>
                <li className="price-top__item">        .</li>
            </ul>
        </div>
    );
};

export default PriceBottom;


